const AmplitudeLeft = () => {
  return (
    <div id='amplitude-left'>
      <img data-amplitude-song-info='cover_art_url' class='album-art' />
      <div class='amplitude-visualization' id='large-visualization'></div>
      <div id='player-left-bottom'>
        <div id='time-container'>
          <span class='current-time'>
            <span class='amplitude-current-minutes'></span>:
            <span class='amplitude-current-seconds'></span>
          </span>
          <div id='progress-container'>
            <div class='amplitude-wave-form'></div>
            <input type='range' class='amplitude-song-slider' />
            <progress
              id='song-played-progress'
              class='amplitude-song-played-progress'
            ></progress>
            <progress
              id='song-buffered-progress'
              class='amplitude-buffered-progress'
              value='0'
            ></progress>
          </div>
          <span class='duration'>
            <span class='amplitude-duration-minutes'></span>:
            <span class='amplitude-duration-seconds'></span>
          </span>
        </div>

        <div id='control-container'>
          <div id='repeat-container'>
            <div class='amplitude-repeat' id='repeat'></div>
            <div
              class='amplitude-shuffle amplitude-shuffle-off'
              id='shuffle'
            ></div>
          </div>

          <div id='central-control-container'>
            <div id='central-controls'>
              <div class='amplitude-prev' id='previous'></div>
              <div class='amplitude-play-pause' id='play-pause'></div>
              <div class='amplitude-next' id='next'></div>
            </div>
          </div>

          <div id='volume-container'>
            <div class='volume-controls'>
              <div class='amplitude-mute amplitude-not-muted'></div>
              <input type='range' class='amplitude-volume-slider' />
              <div class='ms-range-fix'></div>
            </div>
            <div
              class='amplitude-shuffle amplitude-shuffle-off'
              id='shuffle-right'
            ></div>
          </div>
        </div>

        <div id='meta-container'>
          <span data-amplitude-song-info='name' class='song-name'></span>

          <div class='song-artist-album'>
            <span data-amplitude-song-info='artist'></span>
            <span data-amplitude-song-info='album'></span>
          </div>
        </div>
      </div>
    </div>
  );
};

const Song = ({ title, artist, index, url, duration }) => {
  const stopPropagation = e => e.stopPropagation();

  const mouseOver = ({ target }) => {
    return;
    target.style.backgroundColor = '#00A0FF';

    target.querySelectorAll('.song-meta-data .song-title')[0].style.color =
      '#FFFFFF';
    target.querySelectorAll('.song-meta-data .song-artist')[0].style.color =
      '#FFFFFF';

    if (!target.classList.contains('amplitude-active-song-container')) {
      target.querySelectorAll('.play-button-container')[0].style.display =
        'block';
    }

    target.querySelectorAll('img.bandcamp-grey')[0].style.display = 'none';
    target.querySelectorAll('img.bandcamp-white')[0].style.display = 'block';
    target.querySelectorAll('.song-duration')[0].style.color = '#FFFFFF';
  };

  const mouseOut = ({ target }) => {
    return;
    target.style.backgroundColor = '#FFFFFF';
    target.querySelectorAll('.song-meta-data .song-title')[0].style.color =
      '#272726';
    target.querySelectorAll('.song-meta-data .song-artist')[0].style.color =
      '#607D8B';
    target.querySelectorAll('.play-button-container')[0].style.display = 'none';
    target.querySelectorAll('img.bandcamp-grey')[0].style.display = 'block';
    target.querySelectorAll('img.bandcamp-white')[0].style.display = 'none';
    target.querySelectorAll('.song-duration')[0].style.color = '#607D8B';
  };

  const click = ({ target }) => {
    return;
    target.querySelectorAll('.play-button-container')[0].style.display = 'none';
  };

  return (
    <div
      class='song amplitude-song-container amplitude-play-pause'
      data-amplitude-song-index={index}
      onMouseOver={mouseOver}
      onMouseOut={mouseOut}
      onClick={click}
    >
      <div class='song-now-playing-icon-container'>
        <div class='play-button-container'></div>
        <img
          class='now-playing'
          src='https://521dimensions.com/img/open-source/amplitudejs/blue-player/now-playing.svg'
        />
      </div>
      <div class='song-meta-data'>
        <span class='song-title'>{title}</span>
        <span class='song-artist'>{artist}</span>
      </div>
      <a href={url} class='bandcamp-link' onClick={stopPropagation}>
        <img
          class='bandcamp-grey'
          src='https://521dimensions.com/img/open-source/amplitudejs/blue-player/bandcamp-grey.svg'
        />
        <img
          class='bandcamp-white'
          src='https://521dimensions.com/img/open-source/amplitudejs/blue-player/bandcamp-white.svg'
        />
      </a>
      <span class='song-duration'>{duration}</span>
    </div>
  );
};

const AmplitudeRight = () => {
  useEffect(() => {
    document.title = 'Гивинята';

    Amplitude.init({
      songs,
      callbacks: {
        play: function() {
          document.getElementById('album-art').style.visibility = 'hidden';
          document.getElementById('large-visualization').style.visibility =
            'visible';
        },

        pause: function() {
          document.getElementById('album-art').style.visibility = 'visible';
          document.getElementById('large-visualization').style.visibility =
            'hidden';
        }
      },
      waveforms: {
        sample_rate: 50
      }
    });
  }, []);

  return (
    <div id='amplitude-right'>
      {songs.map((song, index) => {
        return (
          <Song
            artist={song.artist}
            title={song.name}
            url={song.url}
            index={index}
          ></Song>
        );
      })}
    </div>
  );
};

const App = () => {
  return (
    <div id='blue-playlist-container'>
      <div id='amplitude-player'>
        <AmplitudeLeft></AmplitudeLeft>
        <AmplitudeRight></AmplitudeRight>
      </div>
    </div>
  );
};

render(<App></App>, document.getElementById('app'));
